// 
import React, { useEffect } from 'react';
import demoImage from './demo.png';

const threshold = [0.1] //  0.01 1% threshold = [0, 0.25, 0.5, 0.75]  0% 25% 50% 75% 

//  IntersectionObserver 
const io = new IntersectionObserver((entries) => { // 
  entries.forEach((item) => { // entries 
    if (item.intersectionRatio <= 0) return // intersectionRatio  
    const {target} = item
    target.src = target.dataset.src //  h5  src ()
    io.unobserve(target); //  callback
  })
}, {
  threshold, // 
});

const ImageLazyLoad = ({ imgsrc, imgClick }) => {
  const imgRef = React.useRef();
  // const onload = () => {
  //   console.log("")
  //   io.observe(imgRef.current)
  // }
  useEffect(() => {
    io.observe(imgRef.current)
    return () => {
      io.disconnect(imgRef.current); // ;
    }
  }, [])
  return (
    <img data-src={imgsrc} src="" ref={imgRef} onClick={imgClick} />
  )
}

ImageLazyLoad.defaultProps = {
  imgClick: () => { },
  imgsrc: demoImage
}

export default ImageLazyLoad;